/* Gallery container */
.videopack-gallery-wrapper {
	max-width: 100%;

	.videopack-gallery-items {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
		gap: 15px;
		margin: 0 auto;

		&[style] {
			grid-template-columns: repeat(var(--gallery-columns), 1fr);
		}
	}

	.gallery-thumbnail {
		position: relative;
		padding: 0;
		padding-top: 56.25%; /* 16:9 Aspect Ratio */
		height: 0; /* Important for padding-top trick */
		cursor: pointer;
		overflow: hidden;
		border: none;
		border-radius: 2%;
		box-sizing: border-box;
		background: none;

		img {
		position: absolute; /* Position image to fill the padded space */
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: block;
		object-fit: cover;
		}

		.mejs-overlay-button {
		position: absolute;
		top: 50%;
		left: 50%;
		transform-origin: 0 0;
		border: none;
		}

		.play-button-container {
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;

			&.kg-video-js-skin svg {
				position: absolute;
				max-width: 90px;
				width: 25%;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -64%);

				.play-button-circle {
					fill-opacity: 0.75;
					fill: #282828;
					stroke: #fff;
					stroke-width: 20;
					stroke-miterlimit: 10;
				}

				.play-button-triangle {
					fill: #ffff;
				}
			}
		}

		.video-title {
			box-sizing: border-box;
			position: absolute;
			bottom: 10px;
			left: 0;
			width: 100%;
			padding: 4px 4px 4px 4px;
			overflow: hidden;
			text-align: center;
			line-height: 18px;

			.video-title-text {
				position: relative;
				color: var(--wp--preset--color--tertiary, white);
				font-size: 14px;
				line-height: 1.5em;
				z-index: 4;
			}

			.video-title-background::before {
				content: "";
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--wp--preset--color--primary, black);
				opacity: 0.4;
				z-index: 2;
			}
		}

		&:hover {
			.video-title-background::before {
				opacity: 0.8;
			}
			.video-title-text {
				color: var(--wp--preset--color--base, white);
			}
			.play-button-container svg .play-button-circle {
				fill-opacity: 0.4;
			}
		}
	}


/* Gallery pagination */
	.videopack-gallery-pagination {
		margin-top: 3px;
		margin-bottom: 3px;

		.videopack-pagination-arrow {
			background: none;
			border: none;

			&.videopack-hidden {
				visibility: hidden;
			}
		}

		.videopack-page-number-div {
			display: inline;
		}

		button {
			&.videopack-page-number {
				display: inline-flex;
				width: 1.75em;
				height: 1.75em;
				margin: 2px;
				background-color: white;
				color: var(--wp--preset--color--primary, black);
				border-radius: 50%;
				font-size: 1.1em;
				border: none;
				text-decoration: none;
				align-items: center;
				justify-content: center;

				&.current-page,
				&:hover {
					background-color: var(--wp--preset--color--primary, #555);
					color: var(--wp--preset--color--white, white);

					span {
						cursor: default;
					}
				}
			}
			span {
				cursor: pointer;
			}
		}

		.videopack-pagination-separator, .videopack-pagination-arrow {
			font-size: 2em;
			font-weight: 100;
			vertical-align: middle;
			display: inline-block;
			margin: 0 0 6px 0;
			padding: 0;
		}
	}

	.videopack-modal-overlay {
		position: fixed;
		top: 0;
		left: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		background: rgba(0, 0, 0, 0.7);
		width: 100vw;
		height: 100vh;
		z-index: 10000;

		.videopack-modal-container {
			position: relative;
			color: black;
			background-color: #000;
			width: 80vw;
			max-height: 90vh;
			z-index: 10002;

			.videopack-wrapper {
				margin: 0;
				padding: 5px;
			}

			.modal-navigation {
				cursor: pointer;
				z-index: 10003;
				position: absolute;
				text-decoration: none;
				top: -26px;
				background-color: transparent;
				border: none;

				&.modal-close {
					right: -10px;
					top: -26px;
					height: 17px;
				}

				&.modal-previous {
					right: 59px;
				}

				&.modal-next {
					right: 25px;
				}
			}

			.wp-video {
				margin-bottom: 0;
			}
		}
	}

